// Reset
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

// Border Box

html {
    box-sizing: border-box;
    font-size: 20px;
    @include breakpoint(med-screen) { font-size: 20px;}
    @include breakpoint(small-screen) { font-size: 16px; }
    //@include breakpoint(small-screen) { font-size: 14px; }
}

*, *:before, *:after {
  box-sizing: inherit;
}


body {
    font-family: $sans-serif;
    line-height: $base-line-height;
    color: $text-color;
    background-color: $background-color-white;
    counter-reset: section;
}

img {
    max-width: 100%;
    vertical-align: middle;
}