//---------  Site Header
.site-header {

    position: absolute;
    z-index: 100;
    left: 0;
    right: 0;
    padding-top: $spacing-unit;

    a {
        color: $text-color;
        border-bottom: none;
    }

    @include breakpoint (small-screen) {
    	padding-left: $small-screen-margin;
    }

}

.site-title {
    font-size: $font-size-baseline;
    font-family: $sans-serif;
    color: $brand-color;

    @include breakpoint(smallest-screen) {
        text-align: left;
        left:$small-screen-margin;
        font-size: $font-size-baseline;
    }
}


.site-nav {
    position: absolute;
    right: $spacing-unit;
    top: $spacing-unit;
}

.site-nav ul {

    font-size: $font-size-6;
    font-family: $sans-serif;
    font-weight: $font-weight-light;


        li {
            margin-left: 0;
            display: inline-block;

            &.active span {
                text-decoration: line-through;
                color: $text-color-secondary;
            }

        }

}




// -------- Footer

footer {
    width: 100%;
    padding: $spacing-unit 0;
    padding-bottom: 0;

    p {
        font-size: $font-size-baseline;
    }

    a {
        border-bottom: none !important;
    }

}




//--------- Content blocks and containers and grids


section[role='main'],
article[role='main'] {
    padding-top: $spacing-unit*2.6;
    padding-top:0;
    padding-bottom: 0;

	@include breakpoint(small-screen) {
        padding-top: 0;
    }
}

.section-top {
    padding-top: $spacing-unit*2.5;
}

.section-bottom {
    padding-bottom: $spacing-unit*1.6;
}

.container-row { width: 100%; }

.container,
%container {
    @extend %clearfix;
    $max-width: $content-width * 1.6;
    max-width: $max-width;
    width: 100%;
    padding-left: $spacing-unit;
 	padding-right: $spacing-unit;
 	padding-bottom: $spacing-unit/2;
    margin: 0 auto;

    &.container-small-right {
        padding-left: 30%;
    }

    &.container-small-left {
        padding-right: 20%;

        @include breakpoint(small-screen) {
            padding-right: $small-screen-margin;
        }
    }

    &.container-small {
    	max-width: $content-width-small;
    	padding: 0;

        @include breakpoint(small-screen) {
            padding-left: $small-screen-margin;
            padding-right: $small-screen-margin;
        }

    }

    &.container-large {
    	padding-right: $spacing-unit;
    }

    &.block {
    	padding-top: $spacing-unit * 1.25;
    	padding-bottom: $spacing-unit/2;

    	@include breakpoint(smallest-screen) {
    		padding-top: $spacing-unit/2;
        	padding-bottom: $spacing-unit;
    	}
    }

    &.container-max {
    	max-width: 100%;
    	padding: 0;
    	width: 100%;
    }

    &.bottom-clear { padding-bottom: 0; }

    &.column {
        float: right;
        padding: 0;
        width: 50%;
        max-width: $max-width/2;

        img { width: 50%; text-align: right;}

        &:first-child { 
            padding-left: $spacing-unit;
        }

        @include breakpoint(small-screen) {
            float: none;
            width: 100%;
            max-width: $max-width;
            padding: 0 !important;
        }


    }

    @include breakpoint(small-screen) {
        padding-left: $small-screen-margin;
        padding-right: $small-screen-margin;
    }

} // container



.container-centered,
%container-centered {
    text-align: center;
    padding: $spacing-unit*1.25 0;
    width:70%;
    max-width: 50rem;
    margin-right: auto;
    margin-left: auto;

    @include breakpoint(small-screen) {
        width:85%;
        padding: $spacing-unit $small-screen-margin;
    }
}


//--------- Formatting classes

.bg-color { background: $background-color; }
.bg-color-clear { background: white; }
.bg-color-alt { background: $background-color-alt;}


.margin-bottom { 
    margin-bottom: $spacing-unit * 2; 
    @include breakpoint(smallest-screen) {
        margin-bottom: $spacing-unit;
    }
}

.margin-bottom-half { margin-bottom: $spacing-unit;}


.margin-top { 
    margin-top: $spacing-unit; 

    @include breakpoint(small-screen) {
        margin-top: $spacing-unit/2;
    }
}
.margin-top-clear { margin-top: 0; }
.margin-bottom-clear { margin-bottom: 0; }
.bottom-clear { margin-bottom: 0; padding-bottom:0; }


//---------- Image grid

.img-grid {
    padding:0;
    width: 100%;
    max-width: $content-width * 1.3;
    margin: $spacing-unit auto;
    padding-left: $spacing-unit;
    padding-right: $spacing-unit;
    @extend %container;

    &.img-grid-small {
        max-width: $content-width-small;
    }

    .caption + img, 
    &:not(.img-grid-2) img + img { margin-top: $spacing-unit; }

	img {
		height: auto;
		border: none;
        width: 100%;
	}

	+ .img-grid { margin-top: 2%; }

    &.img-grid-hero {
        max-width: 100%;
        margin: 0;
        padding: 0;

        @include breakpoint(small-screen) {
            padding-right: 0;
            padding-left: 0;
        }
    }


    @include breakpoint(small-screen) {
        padding-left: $small-screen-margin;
        padding-right: $small-screen-margin;
    }
}

.img-grid-2 img { 
    float: left;
    width: 49%; 
    margin-right: 2%;

    &.last-unit { margin-right: 0; }
}


.img-grid-bg {
    background-color: $img-bg;
}


//------------ PROJECT LIST/GRID LAYOUTS

%animated-background {
	background-image: linear-gradient(180deg, $highlight-color 100%, transparent 0);
    background-size: 0 100%;
    background-size: 100% 0;
    background-repeat: no-repeat;
    transition: background-size 200ms ease;
    &:hover {
        background-size: 100% 100%;
    }
}

.project-grid-line { padding: 0 $spacing-unit/3;}

.project-line-1 {
    text-transform: uppercase;
    letter-spacing: 0;
    @extend %h2;
    font-size: $font-size-baseline;

}

.project-line-2 {
    color: $text-color;
    @extend %p;
    font-size: $font-size-baseline;
}


.project-grid { 
    @extend %clearfix; 
    margin: 0 auto;
}

.project-grid-3 {
    text-align: center;
    width: 80%;
    margin-bottom: $spacing-unit*1.5;
    .project-grid-link {
        margin-top: $spacing-unit*3.4;
        margin-bottom: 0;

        &:nth-of-type(even) { 
            margin-top: -2rem; 
        }
        //&:nth-of-type(2) { margin-top: 0;}
        //&:nth-of-type(4) { margin-top: -$spacing-unit; margin-right: 0; }
        @include breakpoint(small-screen) {
            margin-top: 0 !important;
            margin-bottom: 5%;
        }
    }

    @include breakpoint(med-screen) {
        width: 100%;
    }
    @include breakpoint(small-screen) {
        width: 80%;
    }
    @include breakpoint(smallest-screen) {
        width: 100%;
    }

}

.project-grid-link {
	//width: 22%;
	display: inline-block;
	position: relative;
	//margin-bottom: 4%;
	text-align: center;
    vertical-align: top;
	padding: 0;
    width: 30%;
    margin-right: 4.25%;
    margin-bottom: 5%;

	@extend %animated-background;
	background-image: linear-gradient(180deg, $background-color-alt 36%, transparent 0);
	background-size: 100% 0;

    &.project-grid-alt {
        background-image: linear-gradient(180deg, $highlight-color 40%, transparent 0);
    }

	&:hover { .project-line-1, .project-line-2 {color:$brand-color;} }

    &:nth-of-type(3n+3) { margin-right: 0; }


	@include breakpoint(small-screen) {
		width: 47%;
		margin-right: 4.5%;
        &:nth-of-type(3n+3) { margin-right: 4.5%;} 
		&:nth-of-type(even) { margin-right: 0;}

	}
}



.project-thumb {
	padding-top: $spacing-unit/2;
	padding-bottom: $spacing-unit/2;
	width: 40%;
    width: 50%;
	-webkit-filter: grayscale(100%);
	filter: grayscale(100%);
	opacity: 0.85;

	@include breakpoint(small-screen) {
		opacity: 0.9;
		width: 80%;
    }

}

.project-grid-link:hover > .project-thumb {
	opacity: 1;
	-webkit-filter: none;
	filter: none;
	transition: all 400ms ease;
}



//------------ CASE STUDY LAYOUTS

// project article
.project-case-study {
    margin-top: $spacing-unit*3;
    margin-top: 0;
}

// header
.project-header {
    @extend %container-centered;
    padding-top: 0;
    padding-bottom: $spacing-unit/3;
    max-width: 24rem;
}

.project-hero-img {
    padding-bottom: 0;
    margin-bottom: $spacing-unit/3;
}

// project overview
.project-overview {
    float: left;
    margin-bottom: $spacing-unit;
    width: 60%;

    @include breakpoint(med-screen) {
        width: 100%;
        margin-bottom: $spacing-unit/2;
        //margin-top:$spacing-unit;
    }
}

.project-overview-aside,
.project-overview-main,
.project-overview-info {
    float: left;
}

.project-overview-aside { 
    margin-right: 5%; 
    width:25%;
}

.project-overview-main {
    width: 70%;
}

.project-overview-info {
    width: 35%;
    margin-right: 5%;
    float:left;

    @include breakpoint(med-screen) {
        text-align: left;
        width: 100%;
        font-size: $font-size-baseline !important;
        margin-bottom: $spacing-unit/2;
        margin-top: 0;
    }
}


// content
.project-content {
	padding-top: $spacing-unit;
    padding-bottom: $spacing-unit * 3;

	@include breakpoint(small-screen) {
		padding-top: $spacing-unit;
	}
}

.project-content > .container {
	@extend .container-small;
}


.video-embed {
	text-align: center;
	display: block;
	width: $content-width-small;
	height: $content-width-small * 0.562;
	margin: $spacing-unit 0;
	margin: auto;
	margin-top: $spacing-unit/4;
	margin-bottom: $spacing-unit;

	@include breakpoint(smallest-screen) {
		width: 100%;
		height: auto;
	}
}


//----------------- NEW GRIDS

.grid {
    @extend %clearfix;
    $max-width: $content-width * 1.6;
    max-width: $max-width;
    width: 100%;
    padding-left: $spacing-unit;
    padding-right: $spacing-unit;
    padding-bottom: $spacing-unit/2;
    margin: 0 auto;

    .column {
        margin-right: 3%;
        float:left;

        img.shrink {
            @include breakpoint(smallest-screen) {
            width: 70%;
            margin-right: auto;
            margin-left:auto;
            margin-bottom: $spacing-unit;
            text-align: center;
            display:block;
        }
        }
    }

    .column-1 {
        width: 30%;

        @include breakpoint(smallest-screen) {
            width: 100%;
            margin-right: 0;
        }
    }

    .column-2 {
        width: 66%;

        @include breakpoint(smallest-screen) {
            width: 100%;
            margin-right: 0;
        }

    }

    .column-last { margin-right: 0; }


}




