p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}


h1, h2, h3, h4, h5, h6 {
    margin-bottom: $spacing-unit / 8;
    font-weight: $font-weight-light;
    line-height: $line-height-headline;
}

%normalize-text {
    letter-spacing: normal;
    text-transform: none;
}

.text-centered {
    text-align: center;
}


h1, %h1 {
	font-size: $font-size-1;
}

h2, %h2 {
    font-size: $font-size-2;
}

h3, %h3 {
    font-size: $font-size-3;
}

h3.display {
    font-family: $serif;
    margin-bottom: $spacing-unit/2;
}

h4, %h4 {
    font-size: $font-size-4;
}

h4.display {
    font-family: $serif;
    margin-bottom: $spacing-unit/2;

}

h5, %h5 {
    font-size: $font-size-5;
}

h6, %h6 {
    font-size: $font-size-6;
    font-weight: 600;
}

p, %p {
    text-transform: none;
    font-weight: $font-weight-light;
    letter-spacing: normal;
    font-size: $font-size-baseline;
}

p.secondary-text {
    letter-spacing: none;
    font-size: $font-size-baseline;
    line-height: 1.2;
}

.accent-text {
    @extend %h2;
}

.accent-text-secondary {
    @extend %h4;
}

.secondary-color-text {
    color: $highlight-color-alt;
}

.display-text, %display-text {
    @extend %normalize-text;
    font-family: $serif;
    font-size: $font-size-4;
    font-weight: $font-weight-light;
    margin-bottom: $spacing-unit / 4;
    color: $text-color;
    line-height: 1.4;

    @include breakpoint(small-screen) {
        font-size: $font-size-baseline * 0.8;
    }
}

.display-text + .display-text, 
%text-indent, .text-indent { text-indent: $spacing-unit * 0.5;}

.display-text-huge {
    @extend %display-text;
    font-size: $font-size-2; 
    line-height: 1.4;
    margin-bottom: $spacing-unit*1.5;

    @include breakpoint(small-screen) {
        font-size: $font-size-baseline;
        margin-bottom: $spacing-unit;
    }


}

.display-header, %display-header {
    @extend %display-text;
    margin-bottom: 0;
    font-family: $sans-serif !important;
    font-size: $font-size-baseline;
    margin-bottom: $spacing-unit/2;
}


.caption, %caption {
	font-family: $sans-serif;
    font-weight: $base-font-weight;
	font-size: $font-size-baseline * 0.85;
    line-height: 1.4;

    a {
        border-bottom-width: 1px !important;
    }

    @include breakpoint(small-screen) {
        font-size: $font-size-baseline;
    }
}

ul {
    list-style-position: outside; 
}

ul.list-horizontal {

    list-style:none;

    li {

        display: inline;
        font-family: $serif;
        font-size: $font-size-baseline;

        &:not(:first-child)::before {
            content: "\2014";
            margin-right: 0.15rem;
        }
    }

    &.list-secondary li {
        font-family: $sans-serif-black;
        font-size: $font-size-baseline;
    }
}


.align-right { text-align: right; }


// ------------ SPECIAL LAYOUTS: CASE STUDIES + PROJECT LINK GRID



// Project Case Study

.project-header-focus {
    @extend %caption;    
    color:$text-color;

    span { color: $text-color}

    @include breakpoint(small-screen) {
        margin-bottom: $spacing-unit * 1.5;
    }
}



// Case study content styles

.project-content {

    p, li {
        font-family: $sans-serif;
    }

    p a { border-bottom-color: $highlight-bright; }

    h2 {
        padding-right: 10%;
        margin-top: $spacing-unit;
    }

    ul:not(.list-overview) {
        font-family: $sans-serif;
        list-style-type: none;

        li::before {
            content: "â€¢";
            color: $background-color;
            display: inline-block;
            width: 20px;
            margin-left: -20px;        
            padding-top: 4px;
        }  

        li { margin-left: 20px; }  
    }

} // case study

.project-content {
    h1, h2, h3, h4, h5 {
        @extend h5;
    }
}

.project-content p.caption {
    @extend %caption;

    [aria-label="Back to content"] {
        font-size: 70%;
        border-bottom-width: 1px;
    }
}

.img-grid > .caption {
    display: inline-block;
    padding: 0 $spacing-unit;
    width: 100%;
    text-align: center;
    margin-top: $spacing-unit/4;
    color: $text-color;

    a { color: $text-color; }

    @include breakpoint(small-screen) {
        font-size: $font-size-baseline;
        padding: 0 $spacing-unit/6;
    }
}

.project-overview-info p {
    font-family: $sans-serif-black;
    font-size: $font-size-baseline;
}

.project-overview h2 {
    @extend %caption;
    border-bottom: $brand-color solid 1px;
    padding-bottom: $spacing-unit/12;
}

.list-overview {
    list-style-type: none;
    margin-bottom: 0;
    margin-top: $spacing-unit/4;

    li {
        margin-bottom: $spacing-unit/3;
        @extend %caption;
        color: $text-color;
        width: 45%;
        display: inline-block;
        vertical-align:top;
        margin-right: 5%;
        line-height: 1.2;

        &:nth-child(even) {margin-right: 0;}
        &:last-child { margin-bottom: 0;}

        span {
            display: inline-block;
            width: 100%;
            text-transform: uppercase;
            font-size: 80%;
            letter-spacing: 0.05rem;
        }
    }

    &.list-overview-full li { 
        width: 100%; 
        margin-right: 0;
    }
}








// ------------ LINKS

a {
    text-decoration: none;
    color: inherit;
}

.redirect { position: relative; }
a.redirect:hover,
a.redirect:active,
a.redirect:focus {

    &:after {
        content: "\0279A";
        border-bottom: none;
        font-size: 150%;
        white-space: nowrap;
        position: absolute;
        top: -120%;
        right: 0;
        color: $highlight-bright;
    }
}

a:not(.project-link) {
    color: inherit;    
    transition: $transition;    
    border-bottom: 2px solid $brand-color;

    @include breakpoint(small-screen) {
        border-bottom-width: 1px;
    }

    &:hover {
        background-color: $text-color;//$highlight-color;
        border-bottom-color: $text-color;//$highlight-color;
        color:white;
        cursor: pointer;
    }
}


p a.no-underline { border-bottom: none;}

.button {
    display: inline-block;
    padding: $spacing-unit/5 $spacing-unit/2;
    background-color: $background-color;
    width: auto;
    border-radius: 8px;
    border: none !important;
    margin: auto;
    margin-top: $spacing-unit/3;
    margin-bottom: $spacing-unit/3;
    font-weight: $base-font-weight;

    &:hover {
        background-color: $highlight-color-alt;
    }
}


// -------------------- ORGANIZE LATER


.page-header {
    display: inline-block;
    margin-top: $spacing-unit;
    font-size: $font-size-baseline * 1.4;
    margin-bottom: $spacing-unit*2;
    color: $brand-color;

    &:after {
        content: "\2014";
    }
}








