$primary-light: #F8DBDB;
$primary-med: #FEFBF1; //#7171C1;//#6C7093;//#CD9393;


.parallelogram {
    fill: #A5B5F7;
}

.line-vert { 
    fill: #CD9393;
}

.stripes { 
    fill: none;
    stroke: #FFBDA4;
}

.stripes, .eye-bg {
    stroke-miterlimit: 10;
    stroke-width: 4px;
}

.eye-curve{
    fill: $primary-med;
}

.eye-bg {
    fill: $neutral-lightest;
    stroke: $primary-med;
}

.cls-6{
    fill: $primary-med;
}

.pizza { fill:yellow; stroke: pink;}

#hello {
    width: $header-width;
    padding: 1.1rem;
     line, path {   transition: all 400ms ease; }
}

#hello:hover {
        //s.cls-3, .cls-5 { stroke: $accent-color-alt;}
        .cls-3 { stroke: $accent-color}
        //.cls-1, .cls-2, .cls-4, .cls-6, { fill: $accent-color-alt; }

        .eye, .eye-bg, .eye-curve {
                animation-name: jiggle;
                animation-duration: 900ms;
                animation-iteration-count: infinite;
                transform-origin: 50% 50%; 
                animation-timing-function: ease-in;
                animation-iteration-count: 1;  
                //animation-delay: 100ms;
        }

        .parallelogram {
                animation-name: rightLeft;
                animation-duration: 1100ms;
                animation-iteration-count: infinite;
                transform-origin: 0% 50%;  
                animation-timing-function: ease-out;   
                animation-iteration-count: 1;
                //animation-delay: 100ms;
        }

        .line-vert {
                animation-name: upDown;
                animation-duration: 900ms;
                animation-iteration-count: infinite;
                animation-timing-function: ease-out;
                animation-iteration-count: 1;
        }
}

@keyframes upDown {
        0% {
                transform: translate(0px,0px);
        }

        10% {
                transform: translate(0px,20px);
        }

        50% {
                transform: translate(0px,-60px);
        }        

        100% {
                transform: translate(0px,0px);
        }        
}

@keyframes rightLeft {
        0% {
                transform: translate(0px,0px);
        }

        10% {
                transform: translate(-10px,0px);
        }

        60% {
                transform: translate(5px,0px);
        }

        85% {
                transform: translate(-5px,0px);
        }

        100% {
                transform: translate(0px,0px);
        }
}


@keyframes jiggle {
        0% {
                transform: translate(0px,0px);
        }

        10% {
                transform: translate(5px, -1px);

        }
        30% {
                transform: translate(-5px, 7px);

        }

        60% {
                transform: rotate(20deg)
        }

        70% {
                transform: translate(-6px, -1px);

        }

        100% {
                transform: translate(0px,0px);
        }
}