@font-face {
  font-family: 'Ogg Web'; 
  src: url('fonts/Ogg-Thin.eot');
  src: url('fonts/Ogg-Thin.eot?#iefix') format('embedded-opentype'),
       url('fonts/Ogg-Thin.woff2') format('woff2'),
       url('fonts/Ogg-Thin.woff') format('woff');
  font-weight:  100;
  font-style:   normal;
  font-stretch: normal;
}

.Ogg-Thin-Web {
  font-family: 'Ogg Web';
  font-weight:  100;
  font-style:   normal;
  font-stretch: normal;
}



// -----------------------------------------------------
//
// Global variables
// -----------------------------------------------------


$sans-serif: 'Inter', sans-serif;
$sans-serif-black: 'Archivo Black', sans-serif;
$serif: 'Ogg Web', Georgia, 'Times New Roman', Times, serif;




// Our variables
$base-font-family: 'Ogg Web', Georgia, 'Times New Roman', Times, serif;
$base-font-weight: 400;
$base-line-height: 1.4;
$line-height-headline: 1.25;
$font-weight-light: 300;


$font-size-1: 4.209rem;
$font-size-2: 3.157rem;
$font-size-3: 2.369rem;
$font-size-4: 1.777rem;
$font-size-5: 1.333rem;
$font-size-6: 1rem;
$font-size-7: 0.75rem;

$font-size-baseline: $font-size-6;


$spacing-unit: 2.4rem;
$small-screen-margin: $spacing-unit * 0.75; // for mobile margins
$content-width-small: 28rem;
$content-width-small: 30rem;

$content-width: 28rem;
$content-width: 33rem;


$header-width: 3.25rem;

$transition: all 300ms ease;


// ---------- COLORS

$primary-color: #869C8A;

$accent-color: #F8DBDB;


$highlight-color: $primary-color; // used for link hover states
$highlight-bright: #FF7C6B;
$highlight-color-alt: #E3F368; //#EAD4F7;

$neutral-lightest: #FFF1EB;
$neutral-light: #FFE4DC;
$neutral-light-alt: #E7EADE; //#F1F4E6;

$text-color: #000;
$text-color-secondary: #888;

$brand-color: $text-color;

$background-color: $primary-color;
$background-color-alt: $neutral-light-alt; 
$background-color-light: #F6F7F3;
$background-color-white: #FFFFFF;

$img-bg: $neutral-light-alt;
$accent-border: #FFC4B3;


%sitegradient {
  background-image: linear-gradient(30deg, #637E6E 20%, $background-color);
}




// -----------------------------------------------------
//
// Global mixins, extends
// -----------------------------------------------------


@mixin breakpoint($point) {

  	@if $point == med-screen {
          @media (max-width: 68em) { @content ; }
  	}

  	@else if $point == small-screen {
          @media (max-width: 42em) { @content ; } 
    }

    @else if $point == smallest-screen {
          @media (max-width: 34em) { @content ; } 
    }

} // @include breakpoint


%clearfix {
	&:before,
	&:after {
		content: "";
		display: table;
	}
	&:after { clear: both; }
} // clearfix


.visiblyhidden,
%visiblyhidden {
    position: absolute;
    overflow: hidden; 
    clip: rect(0 0 0 0); 
    height: 1px;
    width: 1px; 
    margin: -1px; 
    padding: 0;
    border: 0; 
}


// -----------------------------------------------------
//
// Import partials from `sass_dir` (defaults to `_sass`)
// -----------------------------------------------------

@import
        "base",
        "typography",
        "layout",
        "illo"
;
